'use strict';

const Item = require('../Item');

class Log extends Item {

  async getEntries() {
    return this.manager.getLogEntries({
      id: this.id,
    });
  }

  static transformGet(item) {
    item = super.transformGet(item);

    delete item.ownerName; // Prepare for back-end change

    return item;
  }

}

module.exports = Log;
