'use strict';

const LogV3 = require('../../HomeyAPIV3/ManagerInsights/Log');

class Log extends LogV3 {

  static transformGet(item) {
    item = super.transformGet(item);

    item.ownerId = item.id;
    item.ownerUri = item.uri;
    item.id = `${item.uri}:${item.id}`;

    delete item.uri;
    delete item.uriObj;

    return item;
  }

}

module.exports = Log;
